/**
 *
 * All styles related to typography
 *
 */
.highlight {
  color: #4b77fd;
}

h1 {
  font-size: 1.5rem;
  line-height: 1.75rem;
}

@media screen and (max-width: 43.74875em) {
  h1 {
    font-size: calc(1.5rem/1.2);
  }
}

.display-1 {
  font-family: "futura-pt", sans-serif;
  font-size: 3.4375rem;
  line-height: 4.5625rem;
  font-weight: 500;
}

@media screen and (max-width: 43.74875em) {
  .display-1 {
    font-size: 2.375rem;
  }
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: red;
  margin: 0;
  padding: 0;
  background: #fefefe;
  font-family: "gill-sans-nova", sans-serif;
  font-weight: 500;
  line-height: 1.5;
  color: #000000;
  font-size: 1rem;
  line-height: 1.3rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  height: auto;
  -ms-interpolation-mode: bicubic;
}

textarea {
  height: auto;
  min-height: 50px;
  border-radius: 0;
}

select {
  box-sizing: border-box;
  width: 100%;
  border-radius: 0;
}

.map_canvas img,
.map_canvas embed,
.map_canvas object,
.mqa-display img,
.mqa-display embed,
.mqa-display object {
  max-width: none !important;
}

button {
  padding: 0;
  appearance: none;
  border: 0;
  border-radius: 0;
  background: transparent;
  line-height: 1;
  cursor: auto;
}

[data-whatinput='mouse'] button {
  outline: 0;
}

pre {
  overflow: auto;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
}

.is-visible {
  display: block !important;
}

.is-hidden {
  display: none !important;
}

a {
  color: inherit;
  font-weight: 600;
  text-decoration: none;
  transition: color 0.1s ease-in-out;
  display: inline-block;
}

.button {
  background-color: #4b77fd;
  line-height: 2.625rem;
  padding: 0 20px;
  border-radius: 3px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.2);
  font-family: "futura-pt", sans-serif;
  color: #FFFFFF;
  letter-spacing: 1.1px;
  transform: translateY(-1px);
  transition: box-shadow 0.2s cubic-bezier(0.25, 0.25, 0.315, 1.35);
  white-space: nowrap;
}

.button:hover {
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}

a:not(.button):hover {
  color: #4b77fd;
}

a:active {
  color: #0000ff;
}

.header {
  height: 50px;
  display: flex;
  position: relative;
  margin-top: 3.1875rem;
}

.header__logo {
  height: 100%;
}

.header__logo img {
  height: inherit;
  width: auto;
}

.footer {
  position: relative;
  color: white;
  font-weight: bold;
  margin-bottom: 20px;
}

.cover {
  min-height: 100vh;
  background-color: #4b77fd;
  position: relative;
}

.cover .bg {
  z-index: 0;
  position: absolute;
  bottom: 0;
}

.cover .container {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 100vh;
}

.cover .header {
  margin-bottom: 2.5rem;
}

.cover .footer {
  margin-top: 3.1875rem;
  margin-bottom: 2.5rem;
}

.cover .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.cover .intro-box {
  display: flex;
  flex-direction: column;
  background-color: white;
  width: 100%;
  min-height: 550px;
  margin-top: auto;
  margin-bottom: auto;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  padding: 3.125rem 5rem 50px 3.75rem;
